.center {
  margin: auto;
}

body {
  height: 100%;
  margin: 0;
}
.mainContainerWidth {
  min-width: 500px;
  max-width: 1600px;
}

.full-height {
  height: 100%;
  background: rgba(65, 61, 61, 0.815);
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
/* Shadows */

.hlink {
  text-shadow: 4px 1px 5px rgb(59, 52, 52);
