/* BackGrounColor */

.bgText {
  background: rgba(217, 252, 252, 0.596);
  border-radius: 30px;
}

.bgNav {
  background: rgb(15, 106, 224);
}

.bgGrad {
  background: rgba(93, 225, 230, 0.815);
}
.bgMain {
  background: linear-gradient(0deg, #4884f1 0, #99fcfc 100%);
}
.bgMainControl {
  background: linear-gradient(0deg, #1099f5 0, #16e6e6 100%);
}
.bgCol {
  background: linear-gradient(0deg, #1099f5 0, #16e6e6 100%);
}
.bgHigh {
  background: linear-gradient(0deg, #1099f5 0, #16e6e6 100%);
}
.bgLib {
  background: linear-gradient(0deg, #1099f5 0, #16e6e6 100%);
}
.bgC {
  background: linear-gradient(0deg, #4884f1 0, #99fcfc 100%);
}
.bgAp {
  background: linear-gradient(0deg, #4884f1 0, #99fcfc 100%);
}
.bgFooter {
  background: rgb(15, 106, 224);
}
.bgGallery {
  background: linear-gradient(0deg, #8610f5 0, #16e6e6 100%);
}

.centered {
  height: 100vh; /* Magic here */

  justify-content: center;
  align-items: center;
}

.mheader {
  padding: 156px 0 100px;
}

.msection {
  padding: 300px 0;
}
.imgTable {
  width: 190px;
  height: 190px;
}

.tbh {
  flex: 2;
}
.asText {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}
.bgApp {
  background: linear-gradient(0deg, #528548 0, #19e6db 100%);
  background-size: cover; /* <------ */
  background-repeat: no-repeat;
  background-position: center center;
}
.bgTb {
  background: rgb(red, green, blue);
  background-size: 100% auto; /* <------ */
  background-repeat: no-repeat;
  background-position: center center;
}
/*------------------Login---------------*/
.logIn {
  height: 100vh; /* Magic here */
  padding-top: 100px;
  justify-content: center;
  align-items: center;
}
.form {
  background: rgba(42, 185, 102, 0.7);
  padding-top: 40px;

  margin: auto;

  height: 400px;
  width: 295px;
  text-align: center;
  border-radius: 50px;

  font-family: "Times New Roman", sans-serif;
  font-size: 16;
  font-weight: 200px;
  padding: 50px 0;
  transition: border 0.5s;
  outline: none;
  color: #fff;
}
.logInInput {
  text-align: center;
  justify-content: center;
  width: 250px;
}
.registerForm {
  height: 100vh; /* Magic here */

  justify-content: center;
  align-items: center;
}
/*-------Programs-----------------*/
.ProgramControl {
  padding: 156px 0 100px;
}
/*Table---ScrollBar-----*/
.my-custom-scrollbar {
  position: relative;
  height: 200px;
  overflow: auto;
}
.table-wrapper-scroll-y {
  display: block;
}
/*========HideButton===========*/
.hide {
  display: none;
}

/*=========Container width==========*/

@media (min-width: 1200px) {
  .container-small {
    width: 1300px;
  }
  .container-large {
    width: 1500px;
  }
}

.container-small,
.container-large {
  max-width: 80%;
}

/*=========OverFlow=========*/
.anyClass {
  overflow-y: scroll;
}
